<template>
  <section style="width: 100%;height: 100%;top: 0;position: absolute" v-show="show">
    <div class="mask" v-show="show" @click="hidePop"></div>
    <div class="pop" v-show="show">
      <div class="prompt-title">提示</div>
      <div class="prompt-input">
        <mw-input :placeholder="placeholder" v-model="v"></mw-input>
      </div>
      <div class="prompt-footer">
        <div class="btn" @click="hidePop">取消</div>
        <div class="btn" @click="ok">确定</div>
      </div>
    </div>
  </section>
</template>

<script>
  import MwInput from "./mw-input";
  export default{
    components: {MwInput},
    name: 'mwPrompt',
    props:{
      show:{
        type:Boolean,
        required:true,
        default:false
      },
      okText:{
        type:String,
        default:'确认'
      },
      cancelText:{
        type:String,
        default:'取消'
      },
      placeholder:{
        type:String,
        default:'请输入',
      },
      okFunction:{
        type:Function,
        required:true
      },
      value:{
        type:String,
        required:true
      }
    },
    data(){
      return {
        v:this.value
      }
    },
    methods: {
      hidePop(){
        this.$emit('hidePop')
      },
      ok(){
        if (this.okFunction) {
          this.okFunction()
        }
      }
    },
    watch:{
      v:function () {
        this.$emit('input',this.v)
      }
    }
  }
</script>

<style type='stylesheet/scss' lang='scss' scoped>
  .hidden{
    overflow-y: hidden;
  }
  .mask{
    width: 100%;height: 100%;position:absolute;left: 0;right: 0;background-color: #282828;opacity:0.4;z-index: 11
  }
  .pop{
    position:absolute;top: 35%;width: 80%;height: 25%;z-index: 15;left: 0;
    right: 0;margin: auto;background-color: white;
    display: flex;flex-direction: column;transition: all .5s;
    border-radius: 15px;
    .prompt-title{
      text-align: center;height: 40px;line-height: 40px;
    }
    .prompt-input{
      flex: 1;
      input{
        width: 100%;
        height: 100%;
        text-align: center;
      }
    }
    .prompt-footer{
      width: 100%;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      .btn{
        flex: 1;
        text-align: center;
      }
    }
  }
</style>



